<template>
  <div>
    <!-- 面包屑区域 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>用户管理</el-breadcrumb-item>
      <el-breadcrumb-item>用户列表</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片区域 -->
    <el-card class="box-card">
      <el-row :gutter="20">
        <el-col :span="8">
          <el-input
            placeholder="请输入内容"
            class="input-with-select"
            v-model="quseyInfo.query"
            clearable
            @clear="getUserList"
            @keyup.enter.native="getUserList"
          >
            <el-button
              type="primary"
              slot="append"
              icon="el-icon-search"
              @click="getUserList"
              >搜索</el-button
            >
          </el-input>
        </el-col>
        <el-col :span="8">
          <el-button type="primary" @click="dialogVisible = true"
            >添加用户</el-button
          >
        </el-col>
      </el-row>
      <el-row>
        <el-table :data="userlist" border stripe>
          <el-table-column type="index" label="#"> </el-table-column>
          <el-table-column prop="username" label="姓名"> </el-table-column>
          <el-table-column prop="email" label="邮箱"> </el-table-column>
          <el-table-column prop="mobile" label="电话"> </el-table-column>
          <el-table-column prop="role_name" label="角色"> </el-table-column>
          <el-table-column label="状态" prop="mg_state">
            <template slot-scope="scope">
              <el-switch
                v-model="scope.row.mg_state"
                @change="userStartChange(scope.row)"
              ></el-switch>
            </template>
          </el-table-column>
          <!-- 添加用户的对话框 -->
          <el-table-column label="操作" width="200">
            <!-- 作用域插槽 -->
            <template slot-scope="scope">
              <!-- 修改按钮 -->
              <el-tooltip effect="dark" content="修改数据" placement="top-start" :enterable="false" >
                <el-button type="primary" icon="el-icon-edit" size="mini" @click="showEditDialog(scope.row.id)"></el-button>
              </el-tooltip>
              <!-- 删除数据按钮 -->
              <el-button type="danger" icon="el-icon-delete" @click="removeUserId(scope.row.id)" size="mini"></el-button>
              <!-- 分配按钮 -->
              <el-tooltip effect="dark" content="分配角色" placement="top-end" :enterable="false" >
                <el-button type="warning" icon="el-icon-setting" size="mini" @click="setRoles(scope.row)"></el-button>
              </el-tooltip>
              <!-- enterable:鼠标是否可以进入tooltip状态里，默认true -->
            </template>
          </el-table-column>
        </el-table>
        <!-- 分页的页码条 -->
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="quseyInfo.pagenum"
          :page-sizes="[1, 2, 6, 10, 20]"
          :page-size="quseyInfo.pagesize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total"
          background
        ></el-pagination>
      </el-row>
    </el-card>
<!-- 添加用户对话框 -->
    <el-dialog title="添加新用户" :visible.sync="dialogVisible" width="40%" @close="addDialogClosed">
      <!-- 内容区 -->
      <el-form :model="addForm" status-icon :rules="addFormRules" ref="addFormRef" label-width="100px">
        <el-form-item label="用户名" prop="username">
          <el-input v-model="addForm.username" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label="密码" prop="password">
          <el-input v-model="addForm.password" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label="邮箱" prop="email">
          <el-input v-model="addForm.email" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label="手机号" prop="mobile">
          <el-input v-model="addForm.mobile" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="addUser"
          >确 定</el-button
        >
      </span>
    </el-dialog>
<!-- 修改用户信息的对话框 -->
    <el-dialog title="修改用户" :visible.sync="editDialogVisible" width="30%"  @close="editDialogClosed">
      <!-- 修改表单 -->
      <el-form ref="editFromRef" :model="editFrom" label-width="80px" :rules="addFormRules">
        <el-form-item label="用户名">
          <el-input v-model="editFrom.username" disabled></el-input>
        </el-form-item>
        <el-form-item label="手机号" prop="mobile">
          <el-input v-model="editFrom.mobile"></el-input>
        </el-form-item>
        <el-form-item label="邮箱" prop="email">
          <el-input v-model="editFrom.email"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="editUserInfo">确 定</el-button>
      </span>
    </el-dialog>
<!-- 分配角色的对话框 -->
<el-dialog
  title="分配角色"
  :visible.sync="setRolesDialogVisible"
  width="50%"
  @close="setRoleDialogClose"
  >
  <div>
    <p>当前用户的名称：{{userInfo.username}}</p>
    <p>当前用户的角色：{{userInfo.role_name}}</p>
    <p>分配新的角色：
      <el-select v-model="selectedRoleId" placeholder="请选择角色">
        <el-option
          v-for="item in rolesList"
          :key="item.id"
          :label="item.roleName"
          :value="item.id">
        </el-option>
      </el-select>
    </p>
  </div>
  <span slot="footer" class="dialog-footer">
    <el-button @click="setRolesDialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="saveRoleId">确 定</el-button>
  </span>
</el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    // 验证邮箱
    var checkEmail = (rule, value, callback) => {
      // 验证邮箱的正则表达式
      const regEmail = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
      // 验证邮箱的是否合法
      if (regEmail.test(value)) {
        return callback()
      }
      callback(new Error('请输入合法的邮箱地址'))
    }
    // 验证手机
    var checkMobile = (rule, value, callback) => {
      const regMobile = /^(?:(?:\+|00)86)?1(?:(?:3[\d])|(?:4[5-7|9])|(?:5[0-3|5-9])|(?:6[5-7])|(?:7[0-8])|(?:8[\d])|(?:9[1|8|9]))\d{8}$/
      if (regMobile.test(value)) {
        return callback()
      }
      callback(new Error('请输入合法的手机号'))
    }

    return {
      quseyInfo: {
        query: '',
        // 页数
        pagenum: 1,
        // 当前显示数据的数量
        pagesize: 2
      },
      userlist: [],
      total: 0,
      dialogVisible: false,
      editDialogVisible:false,
      // 添加表单数据
      addForm: {
        username: '',
        password: '',
        email: '',
        mobile: ''
      },
      // 添加表单的验证规则对象
      addFormRules: {
        username: [
          { required: true, message: '请输入用户名', trigger: 'blur' },
          { min: 2, max: 18, message: '长度在 2~18 个字符', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' },
          { min: 6, max: 18, message: '长度在 6~18 个字符', trigger: 'blur' }
        ],
        email: [
          { required: true, message: '请输入邮箱', trigger: 'blur' },
          { validator: checkEmail, trigger: 'blur' }
        ],
        mobile: [
          { required: true, message: '请输入手机号', trigger: 'blur' },
          { validator: checkMobile, trigger: 'blur' }
        ]
      },
      // 修改用户信息
      editFrom:{},
      // 分配角色对话框
      setRolesDialogVisible:false,
    // 需要分配角色的信息
      userInfo:{},
      // 所有角色的数据列表
      rolesList:[],
      // 已选中角色的ID值
      selectedRoleId:''
    }
  },
  created() {
    this.getUserList()
  },
  methods: {
    // 获取用户数据
    async getUserList() {
      const { data: res } = await this.$http.get('users', {
        params: this.quseyInfo
      })
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      this.userlist = res.data.users
      this.total = res.data.total
      // console.log(res)
    },
    // 监听 pagesize 分页
    handleSizeChange(newSize) {
      this.quseyInfo.pagesize = newSize
      this.getUserList()
    },
    // 监听 页码值 的改变
    handleCurrentChange(newPage) {
      this.quseyInfo.pagenum = newPage
      this.getUserList()
    },
    // 监听状态开关的变化
    async userStartChange(userInfo) {
      const { data: res } = await this.$http.put(
        `users/${userInfo.id}/state/${userInfo.mg_state}`
      )
      if (res.meta.status !== 200) {
        userInfo.mg_state = !userInfo.mg_state
        return this.$message.error('用户状态更新失败')
      }
      return this.$message.success('用户状态更新成功')
    },
    // 监听添加对话框关闭事件的回调函数
    addDialogClosed(){
       this.$refs.addFormRef.resetFields()
    },
    editDialogClosed(){
      this.$refs.editFromRef.resetFields()
    },
    // 点击添加新用户
    addUser(){
      this.$refs.addFormRef.validate(async valid=>{
        // valid:返回值为true和false
        // 表单的预校验失败
        if(!valid) return this.$message.error("添加用户失败，请按要求填写！")

         // 表单的预校验成功，可以发起网络请求
        const {data:res} = await this.$http.post(`users`,this.addForm)
         if (res.meta.status !== 201){
           this.$message.error('添加用户失败')
         } 
        this.$message.success('添加用户成功')
        this.getUserList()
        this.dialogVisible = false
      })
    },
    // 弹出编辑用户的对话框
    async showEditDialog(id){
      this.editDialogVisible = true
      const {data:res} = await this.$http.get( `users/${id}`)
      if(res.meta.status !== 200) return this.$message.error("查询用户信息失败")
      this.editFrom = res.data
    },
    // 验证表单，并修改提交
    editUserInfo(){
      // 校验
      this.$refs.editFromRef.validate(async valid=>{
        if(!valid) return
        // 校验成功，允许网络请求
        const {data:res} = await this.$http.put(`users/${this.editFrom.id}`,{email:this.editFrom.email,mobile:this.editFrom.mobile});
        if (res.meta.status !== 200){
           this.$message.error('修改用户信息更新失败')
         } 
        this.$message.success('用户信息修改成功')
        // 关闭对话框
        this.editDialogVisible = false
        // 、刷新数据
        this.getUserList()
      })
    },
    // 删除用户信息
    async removeUserId(id){
      const confirmResult =await this.$confirm('此操作将永久删除该用户, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).catch(err => err)
        // 确认删除，返回值：confirm
        // 取消删除，返回值：cancel
        if(confirmResult !== 'confirm') return this.$message.info('已取消删除！')
        // 提交删除
      const {data:res} = await this.$http.delete(`users/${id}`)
      if(res.meta.status !== 200) return this.$message.error('删除失败')
      this.$message.success('删除成功')
      this.getUserList()

    },
    // 分配角色按钮，点击显示对话框
    async setRoles(userInfo){
      this.userInfo = userInfo
      // 在展示对话框之前，获取所有角色的列表
      const {data:res} = await this.$http.get('roles')
      if(res.meta.status !== 200) {
        return this.$message.error("获取数据列表失败")
      }
      this.rolesList = res.data
      this.setRolesDialogVisible = true;
    },
    async saveRoleId(){
      if(!this.selectedRoleId){
        return  this.$message.error('请选择要分配的角色！')
      }
      const {data:res} = await this.$http.put(`users/${this.userInfo.id}/role`,{
       rid:this.selectedRoleId
      })
      if(res.meta.status !== 200){
         return  this.$message.error('更新角色失败！')
      }
      this.getUserList()
      this.$message.success('更新角色成功！')
      this.setRolesDialogVisible = false
    },
    // 监听分配对话框关闭事件
    setRoleDialogClose(){
      this.selectedRoleId = ''
      this.userInfo = {}
    }
    
  }
}
</script>
